<template lang="html">

  <div v-for="frm in formulari" v-bind:key="frm" class="formular">
    <div v-for="(text,i) in frm.formular"  v-bind:key="i">
      <h3 v-if="i==0">{{frm.formular[i]}}</h3>
      <p v-if="i!=0">{{frm.formular[i]}}</p>
    </div>

  </div>

</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
export default {

  computed: mapGetters(["formulari"]),
  methods:{
  ...mapActions(['getAllFormulars']),
},
created(){
  this.getAllFormulars();
}
}
</script>

<style lang="css" scoped>
.formular{
  background-color: white;
  color: black;
  border-style: solid;
  border-color: black;
  border-width: medium;
  padding:10px;
  margin-bottom: 5px;
}
</style>
